	</view>
</template>

<script>
	export default {
		data() {
			return {
				active: false
			};
		},
		components:{
			
		},
		onLoad() {
		
		},
		onShow() {
			this.active = true;
		},
		onHide() {
			this.active = false;
		},
		onReady() {
			
		},
		methods: {
