			width: 100%;
			z-index: 3;
			margin: 10upx;
			color: $uni-color-subtitle;
			text-align: center;
			font-size: $uni-font-size-base;
			
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			
			// 控制当前页面中 svg 图标的 大小
			.t-icon{
				width: 40px;
				height: 40px;
			}
